import { useRef, useState } from 'react';
import {
  Button,
  Card,
  Box,
  CardContent,
  CardHeader,
  Menu,
  MenuItem,
  Typography,
  Divider,
  Grid
} from '@material-ui/core';
import { useTranslation } from 'react-i18next';
import { experimentalStyled } from '@material-ui/core/styles';
import ExpandMoreTwoToneIcon from '@material-ui/icons/ExpandMoreTwoTone';
import TransactionsStatisticsChart from './TransactionsStatisticsChart';

const TransactionsStatisticsChartWrapper = experimentalStyled(
  TransactionsStatisticsChart
)(
  ({ theme }) => `
        height: 200px;
`
);

const DotPrimaryLight = experimentalStyled('span')(
  ({ theme }) => `
    border-radius: 22px;
    background: ${theme.colors.primary.lighter};
    width: ${theme.spacing(1.5)};
    height: ${theme.spacing(1.5)};
    display: inline-block;
    margin-right: ${theme.spacing(0.5)};
`
);

const DotPrimary = experimentalStyled('span')(
  ({ theme }) => `
    border-radius: 22px;
    background: ${theme.colors.primary.main};
    width: ${theme.spacing(1.5)};
    height: ${theme.spacing(1.5)};
    display: inline-block;
    margin-right: ${theme.spacing(0.5)};
`
);

function TransactionsStatistics() {
  const { t } = useTranslation();

  const transactions = {
    income: [1769, 791, 1093, 189, 869, 1065, 980, 1419, 380, 1146, 797, 659],
    expenses: [1083, 649, 312, 1538, 1404, 630, 1714, 853, 1765, 1067, 696, 538]
  };

  const generic = {
    month: {
      labels: [
        'Jan',
        'Feb',
        'Mar',
        'Apr',
        'May',
        'Jun',
        'Aug',
        'Sep',
        'Oct',
        'Nov',
        'Dec'
      ]
    }
  };
  const periods = [
    {
      value: 'current_year',
      text: t('Current Year')
    },
    {
      value: 'last_year',
      text: t('Last Year')
    },
    {
      value: 'all_time',
      text: t('All time')
    }
  ];

  const data = {
    bills: '$684.00',
    income: '$3,200.00',
    expenses: '$1596.85',
    taxes: '$785.00'
  };
  const actionRef1 = useRef(null);
  const [openPeriod, setOpenMenuPeriod] = useState(false);
  const [period, setPeriod] = useState(periods[0].text);

  return (
    <Card>
      <CardHeader title={t('Transactions Statistics')} />
      <Divider />
      <CardContent>
        <Box display="flex" alignItems="center" justifyContent="space-between">
          <Button
            size="small"
            variant="outlined"
            ref={actionRef1}
            onClick={() => setOpenMenuPeriod(true)}
            endIcon={<ExpandMoreTwoToneIcon fontSize="small" />}
          >
            {period}
          </Button>
          <Box display="flex" alignItems="center">
            <Typography
              variant="body2"
              color="text.secondary"
              sx={{ display: 'flex', alignItems: 'center', mr: 2 }}
            >
              <DotPrimary />
              {t('income')}
            </Typography>
            <Typography
              variant="body2"
              color="text.secondary"
              sx={{ display: 'flex', alignItems: 'center' }}
            >
              <DotPrimaryLight />
              {t('expenses')}
            </Typography>
          </Box>
        </Box>
        <Menu
          anchorEl={actionRef1.current}
          onClose={() => setOpenMenuPeriod(false)}
          open={openPeriod}
          getContentAnchorEl={null}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left'
          }}
          transformOrigin={{
            vertical: 'top',
            horizontal: 'left'
          }}
        >
          {periods.map((_period) => (
            <MenuItem
              key={_period.value}
              onClick={() => {
                setPeriod(_period.text);
                setOpenMenuPeriod(false);
              }}
            >
              {_period.text}
            </MenuItem>
          ))}
        </Menu>

        <Box height={200} sx={{ py: 3, px: { lg: 2 } }}>
          <TransactionsStatisticsChartWrapper
            data={transactions}
            labels={generic.month.labels}
          />
        </Box>

        <Box sx={{ pt: 8, pb: 0, textAlign: 'center' }}>
          <Grid spacing={3} container>
            <Grid item sm={6}>
              <Typography variant="subtitle2" gutterBottom>
                {t('Income')}
              </Typography>
              <Typography variant="h3">{data.income}</Typography>
            </Grid>
            <Grid item sm={6}>
              <Typography variant="subtitle2" gutterBottom>
                {t('Expenses')}
              </Typography>
              <Typography variant="h3">{data.expenses}</Typography>
            </Grid>
            <Grid item sm={6}>
              <Typography variant="subtitle2" gutterBottom>
                {t('Taxes')}
              </Typography>
              <Typography variant="h3">{data.taxes}</Typography>
            </Grid>
            <Grid item sm={6}>
              <Typography variant="subtitle2" gutterBottom>
                {t('Bills')}
              </Typography>
              <Typography variant="h3">{data.bills}</Typography>
            </Grid>
          </Grid>
        </Box>
      </CardContent>
    </Card>
  );
}

export default TransactionsStatistics;
